/* 
  Codnut Engine v1.0
  written by: Jiwoong Song, Chulwhi Park, and Kyungsoo Lee

  Codnut Engine is a SVG-based math engine that can be used to create interactive math content.

  Codnut Engine has the follwing features:
  - SVG-based rendering
  - Interactive elements
  - Toolbar for selected elements
  - Customizable pre-render and after-render functions
*/

import {
  registerEvent,
  getElement,
  toggleHidden,
  toggleVisibility,
  saveInputItem,
  handleTextCursorSvg,
  getElementsByQuerySelector,
} from './util'

class CodnutDataStorage {
  #data = {}
  constructor() {}

  set(key, value) {
    this.#data[key] = value
  }

  get(key) {
    return this.#data[key]
  }

  clear(key) {
    delete this.#data[key]
  }
}

// Every Math class MUST extend this class
// Override onClear() and initialize() to add custom functionality
export class CodnutDigitalMathClass {
  #version = '1.0'

  // Constants
  #gridDistance = 40
  getGridDistance() {
    return this.#gridDistance
  }

  #snapDistanceThreshold = 10
  getSnapDistanceThreshold() {
    return this.#snapDistanceThreshold
  }

  #dataStorage = new CodnutDataStorage()

  constructor(_root) {
    this.activeElement = null
    this.createElement = null
    this.elements = []

    this.grabbed = null
    this.grabbedCoordinates = { x: 0, y: 0 }

    this.selected = []

    this.dragToAdd = false

    this.grabbedToDND = null

    this.draw = false
    this.grabToMove = false

    this.initialGrab = { x: 0, y: 0 }
    this.initialViewbox = { x: 0, y: 0, width: 1920, height: 1200 }

    /* */
    this.currentViewbox = {}
    this.currentScale = 1
    this.toastSetTimeOut = false
    this.toastIdCounter = 0
    this.toastCount = 0

    if (_root) {
      this.root = _root
    } else {
      this.root = document
    }

    this.keyboardId = 'codnut-keypad'
    this.keyboardInstance = new CodnutDigitalMathKeypad(this.keyboardId)
    this.isComposing = false

    this.isGrid = false
    this.physicalKeyboardInputElement = getElement('codnut-math-input')

    this.physicalKeyboardInputElement.addEventListener('input', (event) => {
      const element = this.selected[0]
      if (element) {
        const isTitle = element.inputTargetClassName.startsWith('title')
        const deleteFlg = event.inputType === 'deleteContentBackward'
        const currentValue = isTitle
          ? event.target.value
          : deleteFlg
          ? ''
          : event.target.value.trim()
        this.onInputCallback(currentValue)
      }
    })

    this.physicalKeyboardInputElement.addEventListener('keydown', (event) => {
      if (event.key === 'Enter') {
        event.preventDefault()
        if (this.isComposing) {
          this.isComposing = false
          return
        }
        const currentValue = event.target.value
        event.target.value = ''
        this.onKeyDownCallBack(currentValue)
      }
    })

    this.physicalKeyboardInputElement.addEventListener('compositionstart', () => {
      this.isComposing = true
    })

    this.physicalKeyboardInputElement.addEventListener('compositionend', () => {
      this.isComposing = false
    })

    this.isRender = true
    this.isDelete = true

    this.#registerKeyboardEvents()

    this.#registerFullscreenEvents()

    /* Move around the board */
    registerEvent('move-board', 'pointermove', (e) => {
      if (!this.grabToMove) return
      let dx = e.offsetX - this.initialGrab.x
      let dy = e.offsetY - this.initialGrab.y
      let reference = this.initialViewbox
      let scale = 2

      let vBox = `${Number(reference[0]) - dx * scale} ${Number(reference[1]) - dy * scale} ${
        reference[2]
      } ${reference[3]}`

      getElement('outermost').setAttribute('viewBox', vBox)

      let vBoxAtt = vBox.split(' ')
      getElement('codnut-digital-math-grid').setAttribute('x', vBoxAtt[0])
      getElement('codnut-digital-math-grid').setAttribute('y', vBoxAtt[1])
      getElement('codnut-digital-math-grid').setAttribute('width', vBoxAtt[2])
      getElement('codnut-digital-math-grid').setAttribute('height', vBoxAtt[3])

      getElement('global-dnd-board').setAttribute('viewBox', vBox)
      getElement('pen-board-svg').setAttribute('viewBox', vBox)
    })

    registerEvent('move-board', 'pointerdown', (e) => {
      this.grabToMove = true
      this.initialGrab = { x: e.offsetX, y: e.offsetY }
      this.initialViewbox = getElement('outermost').getAttribute('viewBox').split(' ')
    })

    registerEvent('move-board', 'pointerup', (e) => {
      this.grabToMove = false
    })
    /************************/

    function isPointerOnPath(mouseX, mouseY, path, tolerance = 10) {
      const pathLength = path.getTotalLength()
      let point, dx, dy

      for (let i = 0; i < pathLength; i += 1) {
        point = path.getPointAtLength(i)
        dx = Math.abs(point.x - mouseX)
        dy = Math.abs(point.y - mouseY)

        if (dx < tolerance && dy < tolerance) {
          return true // Pointer is within tolerance of the path
        }
      }

      return false // Pointer is not on the path
    }

    /* Use Pen */
    registerEvent('pen-board', 'pointermove', (e) => {
      if (!this.draw) return

      let factor = this.getScaleFactor()

      let currentViewbox = getElement('outermost').getAttribute('viewBox').split(' ')
      let p = this.translatePointFromViewBoxToViewBox(
        { x: e.offsetX * factor, y: e.offsetY * factor },
        { xMin: 0, yMin: 0, xMax: 1920, yMax: 1200 },
        {
          xMin: Number(currentViewbox[0]),
          yMin: Number(currentViewbox[1]),
          xMax: Number(currentViewbox[0]) + Number(currentViewbox[2]),
          yMax: Number(currentViewbox[1]) + Number(currentViewbox[3]),
        },
      )
      let mode = getElement('pen-board-svg').getAttribute('mode')
      if (mode === 'pen') {
        let d = this.currentPath.getAttribute('d')
        this.currentPath.setAttribute('d', `${d} L ${p.x},${p.y}`)
      } else if (mode === 'erase') {
        let paths = getElement('pen-board-svg').querySelectorAll('path')
        if (paths) {
          paths.forEach((path) => {
            if (isPointerOnPath(p.x, p.y, path)) {
              path.remove()
            }
          })
        }
      }
    })

    registerEvent('pen-board', 'pointerdown', (e) => {
      this.draw = true

      let factor = this.getScaleFactor()

      let currentViewbox = getElement('outermost').getAttribute('viewBox').split(' ')
      let p = this.translatePointFromViewBoxToViewBox(
        { x: e.offsetX * factor, y: e.offsetY * factor },
        { xMin: 0, yMin: 0, xMax: 1920, yMax: 1200 },
        {
          xMin: Number(currentViewbox[0]),
          yMin: Number(currentViewbox[1]),
          xMax: Number(currentViewbox[0]) + Number(currentViewbox[2]),
          yMax: Number(currentViewbox[1]) + Number(currentViewbox[3]),
        },
      )

      let mode = getElement('pen-board-svg').getAttribute('mode')
      if (mode === 'pen') {
        this.currentPath = document.createElementNS('http://www.w3.org/2000/svg', 'path')
        this.currentPath.setAttribute('fill', 'none')
        this.currentPath.setAttribute('stroke', 'black')
        this.currentPath.setAttribute('stroke-width', '4')
        this.currentPath.setAttribute('id', 'pen-path-' + crypto.randomUUID())
        // Start the path
        this.currentPath.setAttribute('d', `M ${p.x},${p.y}`)
        this.currentPath.classList.add('pen-stroke')
        getElement('pen-board-svg').appendChild(this.currentPath)
      }
    })

    registerEvent('pen-board', 'pointerup', (e) => {
      this.draw = false
    })
    /************************/

    /* Global DND Board */
    registerEvent('global-dnd-board', 'pointermove', this.handleGlobalDndBoardMove.bind(this))
    registerEvent('global-dnd-board', 'pointerup', this.handleGlobalDndBoardPointerUp.bind(this))
    /***********************/

    registerEvent('codnut-toolbar-refresh', 'pointerup', (e) => {
      getElement('codnut-toolbar-refresh').classList.remove('hover-dim')
      // override this function to add clear functionality
      let outermost = getElement('outermost')
      outermost.setAttribute('scale', '3')
      outermost.setAttribute('viewBox', '0 0 1920 1200')

      let grid = getElement('codnut-digital-math-grid')
      grid.setAttribute('x', 0)
      grid.setAttribute('y', 0)
      grid.setAttribute('width', 1920)
      grid.setAttribute('height', 1200)
      grid.setAttribute('viewBox', `0 0 1920 1200`)

      let dot = getElement('codnut-toolbar-zoom-pointer')
      dot.setAttribute('transform', `translate(0, 0)`)

      let globaldnd = getElement('global-dnd-board')
      globaldnd.setAttribute('viewBox', `0 0 1920 1200`)

      getElement('codnut-toolbar-toggle-pen').classList.remove('toolbar-selected')
      getElement('codnut-toolbar-toggle-eraser').classList.remove('toolbar-selected')
      getElement('codnut-toolbar-explore').classList.remove('toolbar-selected')

      getElement('pen-board').classList.remove('hidden')
      getElement('pen-board').classList.add('hidden')

      getElement('codnut-digital-math-grid').classList.remove('invisible')
      getElement('codnut-toolbar-toggle-grid').classList.remove('toolbar-selected')
      getElement('codnut-digital-math-grid').classList.add('invisible')

      getElement('move-board').classList.remove('hidden')
      getElement('move-board').classList.add('hidden')

      getElement('pen-board-svg').innerHTML = ''

      this.onClear()
    })

    registerEvent('codnut-toolbar-remove', 'pointerup', (e) => {})

    registerEvent('codnut-math-trashcan', 'pointerup', (e) => {
      getElement('codnut-math-trashcan').classList.remove('hover-dim')
      if (this.onErase) {
        let result = this.onErase()
        if (result) {
          return
        }
      }
      this.selected.forEach((elem) => {
        getElement(elem.id + '-animation').classList.add('disappear-scale-down')
        this.removeElementById(elem.id)
      })

      const msbr = getElement('multi-select-bounding-rect')
      if (!msbr.classList.contains('hidden')) msbr.classList.add('hidden')

      this.grabbed = null
      this.selected = []
      setTimeout(() => {
        this.render()
      }, 300)
    })

    registerEvent('outermost', 'pointerdown', (e) => {
      if (this.onOuterMostPointerDown) this.onOuterMostPointerDown(e)

      // Pointerdown event fired on outermost / grid
      if (e.srcElement.id === 'outermost' || e.srcElement.id === 'codnut-digital-math-grid') {
        this.grabbed = null

        this.physicalKeyboardInputElement.blur()

        let factor = this.getScaleFactor()

        let currentViewbox = getElement('outermost').getAttribute('viewBox').split(' ')

        let p = this.translatePointFromViewBoxToViewBox(
          { x: e.offsetX * factor, y: e.offsetY * factor },
          { xMin: 0, yMin: 0, xMax: 1920, yMax: 1200 },
          {
            xMin: Number(currentViewbox[0]),
            yMin: Number(currentViewbox[1]),
            xMax: Number(currentViewbox[0]) + Number(currentViewbox[2]),
            yMax: Number(currentViewbox[1]) + Number(currentViewbox[3]),
          },
        )

        this.#dataStorage.set('state', 'multi-select')
        this.#dataStorage.set('multiselect-upperleft-coordinates', { x: p.x, y: p.y })
      }
    })

    registerEvent('outermost', 'pointerup', (e) => {
      if (this.onOuterMostPointerUp) {
        let result = this.onOuterMostPointerUp(e)
        if (result) return
      }

      let currentState = this.#dataStorage.get('state')
      if (currentState && currentState === 'multi-select') {
        const multiSelectArea = getElement('codnut-multiselect-area')
        if (!multiSelectArea?.classList.contains('hidden')) {
          multiSelectArea.classList.add('hidden')

          let x = Number(multiSelectArea.getAttribute('x'))
          let y = Number(multiSelectArea.getAttribute('y'))
          let width = Number(multiSelectArea.getAttribute('width'))
          let height = Number(multiSelectArea.getAttribute('height'))

          this.selected = []
          this.elements.forEach((elem) => {
            if (elem.x > x && elem.x < x + width && elem.y > y && elem.y < y + height) {
              if (!this.selected.includes(elem)) this.selected.push(elem)
            }
          })

          if (this.selected.length > 1) {
            const mbrBR = getElement('multi-select-bounding-rect')
            if (mbrBR.classList.contains('hidden')) {
              mbrBR.classList.remove('hidden')
            }

            let leftMostX = 999999
            let rightMostX = -999999
            let topY = 999999
            let bottomY = -999999
            this.selected.forEach((element) => {
              const elem = element.getBoundingRect()
              if (elem) {
                if (elem.x < leftMostX) leftMostX = elem.x
                if (elem.x + elem.width > rightMostX) rightMostX = elem.x + elem.width
                if (elem.y < topY) topY = elem.y
                if (elem.y + elem.height > bottomY) bottomY = elem.y + elem.height
              }
            })

            mbrBR.setAttribute('x', leftMostX)
            mbrBR.setAttribute('y', topY)
            mbrBR.setAttribute('width', rightMostX - leftMostX)
            mbrBR.setAttribute('height', bottomY - topY)

            this.#dataStorage.set('multi-select-bounding-rect-coords', { x: leftMostX, y: topY })
          }

          this.#dataStorage.clear('state')
          this.render()
          return
        }
        this.#dataStorage.clear('state')
      }
      if (currentState && currentState === 'move-multi-objs') {
        const mbrBR = getElement('multi-select-bounding-rect')

        this.#dataStorage.set('multi-select-bounding-rect-coords', {
          x: mbrBR.getAttribute('x'),
          y: mbrBR.getAttribute('y'),
        })
        this.#dataStorage.clear('state')
      }

      if (this.grabbed) {
        if (this.selected.length > 1) {
          this.selected.forEach((elem) => {
            let transformValue = getElement(elem.id)?.getAttribute('transform')
            const regex = /translate\((-?\d+(?:\.\d+)?),\s*(-?\d+(?:\.\d+)?)\)/
            const matches = transformValue?.match(regex)

            if (matches) {
              elem.setXY(Number(matches[1]) - elem.rx, Number(matches[2]) - elem.ry)
            }
          })
        } else {
          let transformValue = getElement(this.grabbed.id)?.getAttribute('transform')
          const regex = /translate\((-?\d+(?:\.\d+)?),\s*(-?\d+(?:\.\d+)?)\)/
          const matches = transformValue?.match(regex)

          if (matches) {
            this.grabbed.setXY(
              Number(matches[1]) - this.grabbed.rx,
              Number(matches[2]) - this.grabbed.ry,
            )
          }
        }
        this.grabbed = null

        if (this.afterDrag) {
          this.afterDrag()
        }
      } else if (
        (e.srcElement.id === 'outermost' || e.srcElement.id === 'codnut-digital-math-grid') &&
        this.selected.length > 0
      ) {
        this.grabbed = null
        this.selected = []

        const mbrBR = getElement('multi-select-bounding-rect')
        if (!mbrBR.classList.contains('hidden')) {
          mbrBR.classList.add('hidden')
        }

        this.render()
      }
    })

    registerEvent('outermost', 'pointermove', (e) => {
      if (this.onOuterMostPointerMove) {
        let skipGlobalAction = this.onOuterMostPointerMove(e)
        if (skipGlobalAction) return
      }

      // Multi-Select
      const currentState = this.#dataStorage.get('state')
      if (currentState && currentState === 'multi-select') {
        let upperLeft = this.#dataStorage.get('multiselect-upperleft-coordinates')
        if (!upperLeft) return

        let factor = this.getScaleFactor()

        let currentViewbox = getElement('outermost').getAttribute('viewBox').split(' ')

        let p = this.translatePointFromViewBoxToViewBox(
          { x: e.offsetX * factor, y: e.offsetY * factor },
          { xMin: 0, yMin: 0, xMax: 1920, yMax: 1200 },
          {
            xMin: Number(currentViewbox[0]),
            yMin: Number(currentViewbox[1]),
            xMax: Number(currentViewbox[0]) + Number(currentViewbox[2]),
            yMax: Number(currentViewbox[1]) + Number(currentViewbox[3]),
          },
        )

        let underRight = { x: p.x, y: p.y }

        let width = underRight.x - upperLeft.x
        let height = underRight.y - upperLeft.y

        let coordX = upperLeft.x
        let coordY = upperLeft.y

        if (width < 0) {
          coordX = coordX - Math.abs(width)
          width = Math.abs(width)
        }

        if (height < 0) {
          coordY = coordY - Math.abs(height)
          height = Math.abs(height)
        }

        let multiSelectRect = getElement('codnut-multiselect-area')
        if (multiSelectRect && width > 20 && height > 20) {
          if (multiSelectRect.classList.contains('hidden'))
            multiSelectRect.classList.remove('hidden')
          multiSelectRect.setAttribute('x', coordX)
          multiSelectRect.setAttribute('y', coordY)
          multiSelectRect.setAttribute('width', width)
          multiSelectRect.setAttribute('height', height)
        }

        return
      }

      if (this.grabbed) {
        let moveFactor = this.getMoveFactor()

        let xDistance = e.offsetX - this.grabbedCoordinates.x
        let yDistance = e.offsetY - this.grabbedCoordinates.y

        const transformX = this.grabbed.x + xDistance * moveFactor + this.grabbed.rx
        const transformY = this.grabbed.y + yDistance * moveFactor + this.grabbed.ry

        const currentSelected = this.selected
        if (currentSelected.length > 1) {
          for (const elem of currentSelected) {
            const tX = elem.x + xDistance * moveFactor + elem.rx
            const tY = elem.y + yDistance * moveFactor + elem.ry
            this.updateElementTransform(elem.id, tX, tY, elem.rotate)
          }
          const msbRectCoords = this.#dataStorage.get('multi-select-bounding-rect-coords')
          const msbRect = getElement('multi-select-bounding-rect')
          const tX = Number(msbRectCoords.x) + xDistance * moveFactor
          const tY = Number(msbRectCoords.y) + yDistance * moveFactor
          msbRect.setAttribute('x', tX)
          msbRect.setAttribute('y', tY)

          this.#dataStorage.set('state', 'move-multi-objs')

          const w = msbRect.getAttribute('width')
          const h = msbRect.getAttribute('height')
          const msgIcon = getElement('multi-select-group-icon')
          msgIcon?.setAttribute(
            'transform',
            `translate(${tX + Number(w ?? 0)}, ${tY + Number(h ?? 0)})`,
          )
        } else {
          if (this.onGrabbedMove) this.onGrabbedMove(this.grabbed, transformX, transformY)

          this.updateElementTransform(this.grabbed.id, transformX, transformY, this.grabbed.rotate)

          const curCoordinates = this.rotateCoordinates(
            this.grabbed.coordinates,
            transformX,
            transformY,
            this.grabbed.rotate,
          )
          if (curCoordinates) {
            const elements = this.elements.filter((element) => element.id !== this.grabbed.id)
            for (const element of elements) {
              const elementCoordinates = this.rotateCoordinates(
                element.coordinates,
                element.x + element.rx,
                element.y + element.ry,
                element.rotate,
              )
              if (elementCoordinates) {
                for (const curCoordinate of curCoordinates) {
                  for (const coordinate of elementCoordinates) {
                    let a = this.getSnapCoordinateTwoPoint(
                      curCoordinate,
                      coordinate,
                      this.#snapDistanceThreshold,
                    )
                    if (
                      this.getSnapCoordinateTwoPoint(
                        curCoordinate,
                        coordinate,
                        this.#snapDistanceThreshold,
                      )
                    ) {
                      let x = curCoordinate[0] - coordinate[0]
                      let y = curCoordinate[1] - coordinate[1]

                      this.updateElementTransform(
                        this.grabbed.id,
                        transformX - x,
                        transformY - y,
                        this.grabbed.rotate,
                      )

                      this.isGrid = false
                    }
                  }
                }
              }
            }

            // Grid스냅
            if (this.isGrid) {
              for (const coordinate of curCoordinates) {
                const { snapX, snapY } = this.getSnapCoordinate(
                  coordinate,
                  this.#gridDistance,
                  this.#snapDistanceThreshold,
                )

                if (snapX % this.#gridDistance === 0 && snapY % this.#gridDistance === 0) {
                  const x = coordinate[0] - snapX
                  const y = coordinate[1] - snapY

                  this.updateElementTransform(
                    this.grabbed.id,
                    transformX - x,
                    transformY - y,
                    this.grabbed.rotate,
                  )
                  return
                }
              }
            }
          }
        }
      }
    })

    if (this.initialize) this.initialize()
  }

  #registerKeyboardEvents() {
    document.onkeydown = (e) => {
      if (e.code === 'Delete' && this.isDelete) {
        if (this.onErase) {
          let result = this.onErase()
          if (result) {
            return
          }
        }
        this.selected.forEach((elem) => {
          getElement(elem.id + '-animation').classList.add('disappear-scale-down')
          this.removeElementById(elem.id)
        })
        const msbr = getElement('multi-select-bounding-rect')
        if (!msbr.classList.contains('hidden')) msbr.classList.add('hidden')

        this.grabbed = null
        this.selected = []
        setTimeout(() => {
          this.render()
        }, 300)
      }
      if (e.code === 'Space') {
        let board = getElement('move-board')
        if (board.classList.contains('hidden')) board.classList.remove('hidden')

        let elem = getElement('codnut-toolbar-explore')
        if (!elem.classList.contains('toolbar-selected')) elem.classList.add('toolbar-selected')
      }
    }

    document.onkeyup = (e) => {
      if (e.code === 'Space') {
        let board = getElement('move-board')
        if (!board.classList.contains('hidden')) board.classList.add('hidden')

        let elem = getElement('codnut-toolbar-explore')
        if (elem.classList.contains('toolbar-selected')) elem.classList.remove('toolbar-selected')
      }
    }
  }

  #registerFullscreenEvents() {
    const onFullscreenChange = (e) => {
      if (document.fullscreenElement) {
        let elem = getElement('codnut-toolbar-toggle-expand')
        if (!elem.classList.contains('hidden')) elem.classList.add('hidden')
        let elem2 = getElement('codnut-toolbar-toggle-shrink')
        if (elem2.classList.contains('hidden')) elem2.classList.remove('hidden')
      } else {
        let elem = getElement('codnut-toolbar-toggle-shrink')
        if (!elem.classList.contains('hidden')) elem.classList.add('hidden')
        let elem2 = getElement('codnut-toolbar-toggle-expand')
        if (elem2.classList.contains('hidden')) elem2.classList.remove('hidden')
      }
    }

    registerEvent('root', 'fullscreenchange', onFullscreenChange)
  }

  handleGlobalDndBoardMove(e) {
    if (this.grabbedToDND) {
      let grabbed = getElement(this.grabbedToDND.id)
      if (grabbed.classList.contains('invisible')) {
        grabbed.classList.remove('invisible')
      }

      const top = getElementsByQuerySelector('.item-toolbar').offsetTop
      const left = getElementsByQuerySelector('.item-toolbar').offsetLeft
      const isTouch = e.pointerType === 'touch'

      let factor = this.getScaleFactor()
      let x = (e.offsetX + (isTouch ? left : 0)) * factor
      let y = (e.offsetY + (isTouch ? top : 0)) * factor
      let currentViewbox = getElement('outermost').getAttribute('viewBox').split(' ')
      let p = this.translatePointFromViewBoxToViewBox(
        { x, y },
        { xMin: 0, yMin: 0, xMax: 1920, yMax: 1200 },
        {
          xMin: Number(currentViewbox[0]),
          yMin: Number(currentViewbox[1]),
          xMax: Number(currentViewbox[0]) + Number(currentViewbox[2]),
          yMax: Number(currentViewbox[1]) + Number(currentViewbox[3]),
        },
      )

      grabbed.setAttribute('transform', `translate(${p.x}, ${p.y})`)
    }
  }

  handleGlobalDndBoardPointerUp(e) {
    if (this.grabbedToDND) {
      let transformValue = getElement(this.createElement.id)?.getAttribute('transform')
      const regex = /translate\((-?\d+(?:\.\d+)?),\s*(-?\d+(?:\.\d+)?)\)/
      const matches = transformValue.match(regex)

      let elementsAtTop = document.elementsFromPoint(e.clientX, e.clientY)
      let has = false
      elementsAtTop.forEach((elem) => {
        if (elem.id && elem.id.endsWith('toolbar')) has = true
      })

      if (matches) {
        this.createElement.setXY(Number(matches[1]), Number(matches[2]))
      }
      if (!has) this.elements.push(this.createElement)
      if (!has) this.selected = [this.createElement]
      if (!has && this.onElementAdd) this.onElementAdd(this.createElement)

      this.grabbedToDND = null
      this.dragToAdd = null

      getElement('global-dnd-board').innerHTML = ''

      toggleHidden('global-dnd')
      this.activeElement = null
      this.createElement = null
      this.render()
    } else {
      this.addElementAtCenter(this.createElement)

      if (this.elementsToAnimate)
        this.elementsToAnimate.push({ id: this.createElement.id, animation: 'appear-scale-up' })

      this.render()
      if (this.onElementAdd) this.onElementAdd(this.createElement)
    }
  }

  listenToPhysicalKeyboard(onInputCallback, onKeyDownCallBack) {
    if (!onInputCallback && !onKeyDownCallBack) return
    this.onInputCallback = onInputCallback
    this.onKeyDownCallBack = onKeyDownCallBack
    this.physicalKeyboardInputElement.focus()
  }

  getIsGridOn() {
    let grid = getElement('codnut-digital-math-grid')
    return !grid.classList.contains('invisible')
  }

  updateElementTransform(id, tx, ty, rotate) {
    const element = getElement(id)
    if (element) element.setAttribute('transform', `translate(${tx}, ${ty}) rotate(${rotate})`)
  }

  getSurroundVertexCoordinate(arr) {
    return arr.reduce(
      (acc, [x, y]) => {
        return {
          minX: Math.min(acc.minX, x),
          maxX: Math.max(acc.maxX, x),
          minY: Math.min(acc.minY, y),
          maxY: Math.max(acc.maxY, y),
        }
      },
      {
        minX: arr[0][0],
        maxX: arr[0][0],
        minY: arr[0][1],
        maxY: arr[0][1],
      },
    )
  }

  rotateCoordinates(coordinates, x, y, rotate) {
    return coordinates?.map(([pointX, pointY]) => {
      const { x: rotatedX, y: rotatedY } = this.rotatePoint(pointX, pointY, rotate)
      return [rotatedX + x, rotatedY + y]
    })
  }

  rotatePoint(x1, y1, angleDeg) {
    if (angleDeg === 0) return { x: x1, y: y1 }

    const deg = ((angleDeg % 360) + 360) % 360
    const angleRad = (deg * Math.PI) / 180

    const cosTheta = Math.cos(angleRad)
    const sinTheta = Math.sin(angleRad)

    const newX = x1 * cosTheta - y1 * sinTheta
    const newY = x1 * sinTheta + y1 * cosTheta

    return { x: newX, y: newY }
  }

  getSnapCoordinateTwoPoint(point1, point2, distance) {
    return (
      Math.abs(point1[0] - point2[0]) <= distance && Math.abs(point1[1] - point2[1]) <= distance
    )
  }

  getSnapCoordinate(point, grid, distance) {
    const [x, y] = point
    const snapX = Math.round(x / grid) * grid
    const snapY = Math.round(y / grid) * grid
    if (Math.abs(snapX - x) <= distance && Math.abs(snapY - y) <= distance) {
      return { snapX, snapY }
    } else {
      return { snapX: x, snapY: y }
    }
  }

  translatePointFromViewBoxToViewBox(point, sourceViewBox, targetViewBox) {
    const sourceWidthPercentage =
      (point.x - sourceViewBox.xMin) / (sourceViewBox.xMax - sourceViewBox.xMin)
    const sourceHeightPercentage =
      (point.y - sourceViewBox.yMin) / (sourceViewBox.yMax - sourceViewBox.yMin)

    const translatedPoint = {
      x: targetViewBox.xMin + sourceWidthPercentage * (targetViewBox.xMax - targetViewBox.xMin),
      y: targetViewBox.yMin + sourceHeightPercentage * (targetViewBox.yMax - targetViewBox.yMin),
    }
    return translatedPoint
  }

  startDragAndDropToAdd(event) {
    if (this.createElement) {
      this.grabbedToDND = this.createElement
      getElement('global-dnd-board').innerHTML += this.grabbedToDND.svg()
      toggleVisibility(this.grabbedToDND.id)
      toggleHidden('global-dnd')
    }
  }

  listenToKeyboard(tag, onPress, onCancel, onPressBack, onConfirm, onRefresh) {
    this.keyboardInstance.registerListner(getElement(tag))
    registerEvent(tag, 'codnut-keypad-pressed', (e) => {
      if (onPress) onPress(e.detail)
    })

    registerEvent(tag, 'codnut-keypad-cancel', (e) => {
      if (onCancel) onCancel(e.detail)
    })

    registerEvent(tag, 'codnut-keypad-back', (e) => {
      if (onPressBack) onPressBack(e.detail)
    })

    registerEvent(tag, 'codnut-keypad-confirm', (e) => {
      if (onConfirm) onConfirm(e.detail)
    })

    registerEvent(tag, 'codnut-keypad-refresh', (e) => {
      if (onRefresh) onRefresh(e.detail)
    })
  }

  render() {
    this.isGrid = getElement('codnut-digital-math-grid').classList.value !== 'invisible'
    this.dragToAdd = false

    let playground = getElement('playground')
    playground.innerHTML = ''

    /*const elemsWOConfig = this.elements.filter(
      (elem) =>
        elem.type && (elem.type !== 'ma1p03301-config1' || elem.type !== 'ma1p03301-config2'),
    )
    const elemsWConfig = this.elements.filter(
      (elem) =>
        elem.type && (elem.type === 'ma1p03301-config1' || elem.type === 'ma1p03301-config2'),
    )

    this.elements = elemsWOConfig.concat(elemsWConfig)
*/
    let curElements = this.getElements()
    curElements.forEach((elem) => {
      elem.parent = this
      playground.innerHTML += elem.svg()
    })

    playground.innerHTML += this.getCustomSvg()

    curElements.forEach((elem) => {
      if (elem.registerEvent) elem.registerEvents()
    })

    if (this.onRender) this.onRender()
  }

  getScaleFactor() {
    let originalWidth = 1920
    let currentScreenWidth = getElement('codnut-parent-element').offsetWidth
    //let currentScreenHeight = getElement('codnut-parent-element').offsetHeight;

    let scaleFactor = originalWidth / currentScreenWidth

    return scaleFactor
  }

  getMoveFactor() {
    let originalWidth = 1920
    let currentScreenWidth = getElement('codnut-parent-element').offsetWidth
    //let currentScreenHeight = getElement('codnut-parent-element').offsetHeight;

    let moveFactor = originalWidth / currentScreenWidth

    let currentScale = Number(getElement('outermost').getAttribute('scale'))
    if (currentScale === 4) moveFactor *= 1.2
    else if (currentScale === 5) moveFactor *= 1.4
    else if (currentScale === 2) moveFactor *= 0.8
    else if (currentScale === 1) moveFactor *= 0.6

    return moveFactor
  }

  addElement(element, focus = true) {
    if (focus) {
      this.selected = [element]
    }
    this.elements.push(element)
  }

  addElementAtCenter(element, focus = true) {
    let currentViewbox = getElement('outermost').getAttribute('viewBox').split(' ')
    let center = {
      x: (Number(currentViewbox[0]) * 2 + Number(currentViewbox[2])) / 2,
      y: (Number(currentViewbox[1]) * 2 + Number(currentViewbox[3])) / 2,
    }

    let hasElement = false
    do {
      hasElement = false
      this.elements.forEach((elem) => {
        if (elem.x === center.x && elem.y === center.y) {
          hasElement = true
        }
      })
      if (hasElement) {
        center = { x: center.x + 40, y: center.y + 40 }
      }
    } while (hasElement)

    element.setXY(center.x, center.y)

    if (focus) {
      this.selected = [element]
    }
    this.elements.push(element)
  }

  getElements() {
    return this.elements
  }

  /* Use Id to  */
  getElementById(id) {
    return this.elements.find((elem) => elem.id === id)
  }

  /* */
  removeElementById(id) {
    this.elements = this.elements.filter((elem) => elem.id !== id)
  }

  setElements(elements) {
    this.elements = elements
  }

  newCreateElement(tag, element, params) {
    registerEvent(tag, 'pointerdown', (e) => {
      this.lastClicked = new Date().getTime()
      this.dragToAdd = true
      this.activeElement = getElement(tag)
      this.createElement = params ? new element(...params) : new element(0, 0)
      this.createElement.parent = this
      this.grabbedCoordinates = { x: e.offsetX, y: e.offsetY }
    })

    registerEvent(tag, 'pointermove', (e) => {
      if (e.pointerType === 'touch' && new Date().getTime() - this.lastClicked < 100) return

      this.handleGlobalDndBoardMove(e)

      if (!this.dragToAdd) return
      if (this.grabbedToDND) return

      this.startDragAndDropToAdd(e)
    })

    registerEvent(tag, 'pointerup', this.handleGlobalDndBoardPointerUp.bind(this))
  }

  popupKeyboard(x, y, rx = 0, ry = 0) {
    let pad = getElement(this.keyboardId)
    let padWidth = pad.getAttribute('width')
    let padHeight = pad.getAttribute('height')

    let outermostVbox = getElement('outermost').getAttribute('viewBox').split(' ')

    let calculatedWidth = Number(outermostVbox[2]) + Number(outermostVbox[0])
    let calculatedHeight = Number(outermostVbox[3]) + Number(outermostVbox[1])

    let popupX = x
    let popupY = y
    if (x + Number(padWidth) + rx / 2 + 40 >= calculatedWidth) {
      popupX -= Number(padWidth) + rx / 2 + 40
    } else {
      popupX += rx / 2 + 40
    }

    if (y + Number(padHeight) - 30 >= calculatedHeight) {
      popupY -= Number(padHeight) - 30
    } else {
      popupY -= 30
    }

    getElement(this.keyboardId).setAttribute('transform', `translate(${popupX}, ${popupY})`)
    getElement(this.keyboardId).classList.remove('hidden')
  }

  dismissKeyboard() {
    let event = new CustomEvent('codnut-keypad-confirm', { detail: '' })

    let keypadElem = getElement(this.keyboardId)
    this.keyboardInstance?.listner?.dispatchEvent(event)
    this.keyboardInstance?.registerListner(null)
    if (!keypadElem.classList.contains('hidden')) {
      keypadElem.classList.add('hidden')
    }
  }

  getCustomSvg() {
    return `<g id="custom-svg" class="hidden"></g>`
  }

  toast(message, obj, callback) {
    const { color = '#E6EBF5', backgroundColor = '#040C1E', ms = 3000, bottom } = obj || {}

    const toastContainer = getElement('toast-container')
    if (toastContainer.children.length > this.toastCount) return

    const toastId = `toast-message-${this.toastIdCounter++}`
    const toastMessage = document.createElement('div')
    toastMessage.id = toastId
    toastMessage.style.backgroundColor = backgroundColor
    toastMessage.style.color = color
    toastMessage.style.position = 'relative'
    toastMessage.style.fontSize = '1em'
    toastMessage.style.display = 'flex'
    toastMessage.style.justifyContent = 'space-between'
    toastMessage.style.position = 'relative'
    toastMessage.style.padding = '0.75em 4em'
    toastMessage.style.borderRadius = '16px'
    toastMessage.style.alignItems = 'center'
    toastMessage.style.flex = '1 1 auto'

    if (bottom) {
      toastMessage.style.bottom = bottom
    }

    const span = document.createElement('span')
    span.style.fontSize = '1em'

    span.innerHTML = message
    toastMessage.appendChild(span)

    const closeButton = document.createElementNS('http://www.w3.org/2000/svg', 'svg')
    closeButton.setAttribute('width', '24')
    closeButton.setAttribute('height', '24')
    closeButton.setAttribute('viewBox', '0 0 24 24')
    closeButton.setAttribute('fill', 'none')
    closeButton.setAttribute('xmlns', 'http://www.w3.org/2000/svg')
    closeButton.style.cursor = 'pointer'
    closeButton.style.marginLeft = '8px'
    closeButton.style.position = 'absolute'
    closeButton.style.right = '16px'

    closeButton.innerHTML = `
    <path fill-rule="evenodd" clip-rule="evenodd" d="M5.46967 5.46967C5.76256 5.17678 6.23744 5.17678 6.53033 5.46967L12 10.9393L17.4697 5.46967C17.7626 5.17678 18.2374 5.17678 18.5303 5.46967C18.8232 5.76256 18.8232 6.23744 18.5303 6.53033L13.0607 12L18.5303 17.4697C18.8232 17.7626 18.8232 18.2374 18.5303 18.5303C18.2374 18.8232 17.7626 18.8232 17.4697 18.5303L12 13.0607L6.53033 18.5303C6.23744 18.8232 5.76256 18.8232 5.46967 18.5303C5.17678 18.2374 5.17678 17.7626 5.46967 17.4697L10.9393 12L5.46967 6.53033C5.17678 6.23744 5.17678 5.76256 5.46967 5.46967Z" fill="white"/>
  `

    closeButton.addEventListener('pointerup', () => {
      if (callback) callback()
      if (toastContainer.contains(toastMessage)) {
        toastContainer?.removeChild(toastMessage)
      }
    })

    toastMessage.appendChild(closeButton)
    if (toastContainer.firstChild) {
      toastContainer.insertBefore(toastMessage, toastContainer.firstChild)
    } else {
      toastContainer.appendChild(toastMessage)
    }

    if (ms !== Infinity) {
      setTimeout(() => {
        if (toastContainer.contains(toastMessage)) {
          toastContainer?.removeChild(toastMessage)
        }
      }, ms)
    }
  }

  inputPointerUp(e) {
    if (this.table) {
      const container = getElement(this.table.id)

      const classList = e.target.classList.value.split(' ')

      const curTarget = classList[1]
      const element = container.querySelector(`.${curTarget}`)
      const oText = element.getAttribute('data-original-text')

      const inputTag = getElement('codnut-math-input')
      inputTag.value = curTarget.startsWith('title') ? oText?.trim() ?? '' : ' '

      if (!this.table.isEdit || !classList.includes('input')) return
      this.table.inputTargetClassName = classList[1]
      this.table.inputTargetClassNames = container.querySelectorAll('.input-target')
      this.listenToPhysicalKeyboard(
        this.table.handleInput.bind(this.table),
        this.table.handleKeyDown.bind(this.table),
      )

      for (let i = 0; i < this.table.inputTargetClassNames.length; i++) {
        const item = this.table.inputTargetClassNames[i]
        item.nextElementSibling.setAttribute('stroke', '#E6EBF5')
      }

      element?.nextElementSibling.setAttribute('stroke', '#000')
      if (
        this.table.inputTargetClassName.startsWith('title') ||
        this.table.inputTargetClassName.startsWith('unit')
      ) {
        element.textContent = oText

        const textElement = document.createElementNS('http://www.w3.org/2000/svg', 'text')
        textElement.setAttribute('font-size', 28)
        textElement.textContent = oText
        container.appendChild(textElement)

        const titleWidth = textElement.getComputedTextLength()
        const titleRectWidth = Number(element.nextElementSibling.getAttribute('width') ?? 0) - 52
        if (titleWidth >= titleRectWidth) {
          let start = 0
          let end = oText.length
          let middle

          // 이진 탐색을 통해 시작 인덱스를 찾음
          while (start < end) {
            middle = Math.floor((start + end) / 2)
            const substring = oText.substring(middle)
            element.textContent = substring
            const newTextWidth = element.getComputedTextLength()

            if (newTextWidth > titleRectWidth) {
              start = middle + 1
            } else {
              end = middle
            }
          }

          element.textContent = oText.substring(start)
        }
      }
      handleTextCursorSvg(this.table.id, element)
      saveInputItem(this.table)
    }
  }

  getTableTitleTextRender() {
    const elements = this.getElements()
    for (const element of elements) {
      const container = getElement(element.id)
      const titleContainers = container.querySelectorAll('.text-title')
      for (const titleContainer of titleContainers) {
        if (!titleContainer.textContent?.trim() && titleContainer.dataset.placeHolder?.trim()) {
          titleContainer.textContent = titleContainer.dataset.placeHolder.trim()
          continue
        }

        const title = titleContainer.textContent
        const titleWidth = titleContainer.getComputedTextLength()
        const titleRectWidth = Number(titleContainer.dataset.rectWidth ?? 0)

        titleContainer.textContent = title

        if (titleWidth > titleRectWidth) {
          let start = 0
          let end = title.length
          let middle

          while (start < end) {
            middle = Math.floor((start + end) / 2)
            titleContainer.textContent = title.slice(0, middle) + '...'
            const newTextWidth = titleContainer.getComputedTextLength()
            if (newTextWidth > titleRectWidth) {
              end = middle - 1
            } else {
              start = middle + 1
            }
          }

          titleContainer.textContent = title.slice(0, end) + '...'
        }
      }
    }
  }
}

export class CodnutDigital3DMathClass {
  constructor() {
    this.#registerFullscreenEvents()

    function isPointerOnPath(mouseX, mouseY, path, tolerance = 10) {
      const pathLength = path.getTotalLength()
      let point, dx, dy

      for (let i = 0; i < pathLength; i += 1) {
        point = path.getPointAtLength(i)
        dx = Math.abs(point.x - mouseX)
        dy = Math.abs(point.y - mouseY)

        if (dx < tolerance && dy < tolerance) {
          return true // Pointer is within tolerance of the path
        }
      }

      return false // Pointer is not on the path
    }

    /* Use Pen */
    registerEvent('pen-board', 'pointermove', (e) => {
      if (!this.draw) return

      let factor = this.getScaleFactor()

      let currentViewbox = [0, 0, 1920, 1200]
      let p = this.translatePointFromViewBoxToViewBox(
        { x: e.offsetX * factor, y: e.offsetY * factor },
        { xMin: 0, yMin: 0, xMax: 1920, yMax: 1200 },
        {
          xMin: Number(currentViewbox[0]),
          yMin: Number(currentViewbox[1]),
          xMax: Number(currentViewbox[0]) + Number(currentViewbox[2]),
          yMax: Number(currentViewbox[1]) + Number(currentViewbox[3]),
        },
      )
      let mode = getElement('pen-board-svg-three').getAttribute('mode')
      if (mode === 'pen') {
        let d = this.currentPath.getAttribute('d')
        this.currentPath.setAttribute('d', `${d} L ${p.x},${p.y}`)
      } else if (mode === 'erase') {
        let paths = getElement('pen-board-svg-three').querySelectorAll('path')
        if (paths) {
          paths.forEach((path) => {
            if (isPointerOnPath(p.x, p.y, path)) {
              path.remove()
            }
          })
        }
      }
    })

    registerEvent('pen-board', 'pointerdown', (e) => {
      this.draw = true

      let factor = this.getScaleFactor()

      let currentViewbox = [0, 0, 1920, 1200]
      let p = this.translatePointFromViewBoxToViewBox(
        { x: e.offsetX * factor, y: e.offsetY * factor },
        { xMin: 0, yMin: 0, xMax: 1920, yMax: 1200 },
        {
          xMin: Number(currentViewbox[0]),
          yMin: Number(currentViewbox[1]),
          xMax: Number(currentViewbox[0]) + Number(currentViewbox[2]),
          yMax: Number(currentViewbox[1]) + Number(currentViewbox[3]),
        },
      )

      let mode = getElement('pen-board-svg-three').getAttribute('mode')
      if (mode === 'pen') {
        this.currentPath = document.createElementNS('http://www.w3.org/2000/svg', 'path')
        this.currentPath.setAttribute('fill', 'none')
        this.currentPath.setAttribute('stroke', 'black')
        this.currentPath.setAttribute('stroke-width', '4')
        this.currentPath.setAttribute('id', 'pen-path-' + crypto.randomUUID())
        // Start the path
        this.currentPath.setAttribute('d', `M ${p.x},${p.y}`)
        this.currentPath.classList.add('pen-stroke')
        getElement('pen-board-svg-three').appendChild(this.currentPath)
      }
    })

    registerEvent('pen-board', 'pointerup', (e) => {
      this.draw = false
    })
    /************************/
  }

  #registerFullscreenEvents() {
    const onFullscreenChange = (e) => {
      if (document.fullscreenElement) {
        let elem = getElement('codnut-toolbar-toggle-expand')
        if (!elem.classList.contains('hidden')) elem.classList.add('hidden')
        let elem2 = getElement('codnut-toolbar-toggle-shrink')
        if (elem2.classList.contains('hidden')) elem2.classList.remove('hidden')
      } else {
        let elem = getElement('codnut-toolbar-toggle-shrink')
        if (!elem.classList.contains('hidden')) elem.classList.add('hidden')
        let elem2 = getElement('codnut-toolbar-toggle-expand')
        if (elem2.classList.contains('hidden')) elem2.classList.remove('hidden')
      }
    }

    registerEvent('root', 'fullscreenchange', onFullscreenChange)
  }

  getScaleFactor() {
    let originalWidth = 1920
    let currentScreenWidth = getElement('codnut-parent-element').offsetWidth
    //let currentScreenHeight = getElement('codnut-parent-element').offsetHeight;

    let scaleFactor = originalWidth / currentScreenWidth

    return scaleFactor
  }

  translatePointFromViewBoxToViewBox(point, sourceViewBox, targetViewBox) {
    const sourceWidthPercentage =
      (point.x - sourceViewBox.xMin) / (sourceViewBox.xMax - sourceViewBox.xMin)
    const sourceHeightPercentage =
      (point.y - sourceViewBox.yMin) / (sourceViewBox.yMax - sourceViewBox.yMin)

    const translatedPoint = {
      x: targetViewBox.xMin + sourceWidthPercentage * (targetViewBox.xMax - targetViewBox.xMin),
      y: targetViewBox.yMin + sourceHeightPercentage * (targetViewBox.yMax - targetViewBox.yMin),
    }
    return translatedPoint
  }
}

export class CodnutDigitalMathElement {
  constructor(_x, _y, _drag = true, _registerEvents = true) {
    // unique id
    this.id = crypto.randomUUID()

    // x, y coordinates
    this.x = _x
    this.y = _y

    this.parent = ''
    this.drag = _drag
    this.registerEvent = _registerEvents
    this.lastClicked = 0
    this.dimmed = false

    this.rotate = 0
    this.rx = 0
    this.ry = 0

    this.inputTargetClassName = ''
    this.inputTargetClassNames = []
    this.inputLength = 1
  }

  getParent() {
    return this.parent
  }

  setXY(_x, _y) {
    this.x = _x
    this.y = _y
  }

  isFocused() {
    if (!this.parent) return false

    let focusedList = this.parent.selected
    let includes = false
    focusedList.forEach((elem) => {
      if (elem.id === this.id) includes = true
    })
    return includes
  }

  svg(focused = false) {
    return `
    <g
      transform="translate(${this.x + this.rx}, ${this.y + this.ry}) rotate(${this.rotate})"
      cursor=${this.drag ? 'pointer' : 'default'}
      id=${this.id}
    >
      <g id="${this.id + '-animation'}">
        ${this.getSvg()}
        ${(focused || this.isFocused()) && this.getFocus && this.getFocus()}
        ${this.dimmed && this.getDim()}
      </g>
    </g>`
  }

  registerEvents() {
    if (this.onAnimation) this.onAnimation()

    registerEvent(this.id, 'pointermove', (e) => {
      if (this.onPointerMove) this.onPointerMove(e)
    })

    registerEvent(this.id, 'pointerup', (e) => {
      if (this.onPointerUp) this.onPointerUp(e)
    })

    registerEvent(this.id, 'pointerdown', (e) => {
      if (new Date().getTime() - this.lastClicked < 300) {
        if (this.onDoubleClick) this.onDoubleClick(e)
        return
      }

      this.lastClicked = new Date().getTime()

      if (this.onPointerDown) this.onPointerDown(e)

      if (this.drag) {
        this.parent.grabbed = this
        this.parent.grabbedCoordinates = { x: e.offsetX, y: e.offsetY }
      }

      let cur = this.parent.getElementById(this.id)
      if (this.parent.selected.length <= 1) {
        this.parent.removeElementById(this.id)
        this.parent.addElement(cur)
        this.parent.selected = [this]
      }

      if (this.parent.isRender) this.parent.render()
    })
  }

  handleInput(text) {
    if (this.id && this.inputTargetClassName) {
      const curElement = getElement(this.id)
      const element = curElement?.querySelector(`.${this.inputTargetClassName}`)
      if (element) {
        const { length = 1, type } = element.dataset
        this.inputLength = Number(length)

        if (type === 'number' && isNaN(Number(text))) {
          this.parent.toast(`숫자만 입력 할 수 있습니다.`)
          this.parent.physicalKeyboardInputElement.value = text.replace(/\D/g, '')
          return
        }

        if (text.length > this.inputLength) {
          this.parent.toast(`${this.inputLength} 글자 까지 입력 할 수 있습니다.`)
          this.parent.physicalKeyboardInputElement.value = text.substring(0, this.inputLength)
          return
        }

        this.parent.physicalKeyboardInputElement.value = text
        element.setAttribute('fill', '#000')
        element.classList.remove('text-title-gray')
        element.textContent = text.substring(0, Number(length))

        if (this.inputTargetClassName.startsWith('title')) {
          let originalText = element.getAttribute('original-text')
          const str = this.inputTargetClassName.substring(5)
          const n = str ? Number(str) : 0

          this.titleOriginalText[n] = text.substring(0, Number(length))
          if (originalText !== null) {
            this.titleOriginalText[n] = originalText
            let additionalText = text.substring(0, Number(length))
            this.titleOriginalText[n] += additionalText
          }

          const titleWidth = element.getComputedTextLength()
          const titleRectWidth = Number(element.nextElementSibling.getAttribute('width') ?? 0) - 52
          if (titleWidth >= titleRectWidth) {
            let start = 0
            let end = text.length
            let middle

            // 이진 탐색을 통해 시작 인덱스를 찾음
            while (start < end) {
              middle = Math.floor((start + end) / 2)
              const substring = text.substring(middle)
              element.textContent = substring
              const newTextWidth = element.getComputedTextLength()

              if (newTextWidth > titleRectWidth) {
                start = middle + 1
              } else {
                end = middle
              }
            }

            element.textContent = this.titleOriginalText[n].substring(start)
          }
        }
        handleTextCursorSvg(null, element)
        saveInputItem(this)
      }
    }
  }

  handleKeyDown() {
    saveInputItem(this)
    if (this.inputTargetClassName === 'unitText') {
      this.isUnitToolbar = false
      this.parent.render()
      return
    }

    if (this.parent.isEnterRender) {
      this.parent.render()
    } else {
      for (let i = 0; i < this.inputTargetClassNames.length; i++) {
        const item = this.inputTargetClassNames[i]
        const classList = item.classList.value.split(' ')
        if (
          classList.includes(this.inputTargetClassName) &&
          i < this.inputTargetClassNames.length
        ) {
          const element = this.inputTargetClassNames[i + 1]
          if (element) {
            this.parent.physicalKeyboardInputElement.value = ' '
            item.nextElementSibling.setAttribute('stroke', '#E6EBF5')

            element.nextElementSibling.setAttribute('stroke', '#000')
            this.inputTargetClassName = element.classList.value.split(' ')[1]
            handleTextCursorSvg(this.id, element)
          }
          break
        }
      }
    }
  }

  getBoundingRect() {
    if (this.boundingRect) return this.boundingRect()
    else return null
  }
}

export class CodnutDigitalMathKeypad {
  constructor(_keyboardId = 'codnut-keypad') {
    this.keypad = getElement(_keyboardId)
    this.listner = null

    Array.from(this.keypad.querySelectorAll('.keypad-item')).forEach((e) => {
      e.addEventListener('pointerup', (e) => {
        const { keypad } = e.currentTarget.dataset
        if (keypad) {
          if (['confirm', 'refresh', 'cancel', 'back'].includes(keypad)) {
            const detail = ''
            let event = new CustomEvent(`codnut-keypad-${keypad}`, { detail })
            this.listner?.dispatchEvent(event)
            if (keypad !== 'back') getElement(_keyboardId).classList.add('hidden')
          } else {
            const detail = isNaN(Number(keypad)) ? keypad : Number(keypad)
            let event = new CustomEvent('codnut-keypad-pressed', { detail })
            this.listner?.dispatchEvent(event)
          }
        }
      })
    })
  }
  registerListner(listner) {
    this.listner = listner
  }
}
