export class HA1P02801Class extends CodnutDigitalMathClass {
  constructor() {
    super()
  }

  onRender() {
    let curSel = this.selected[0]
    if (curSel) {
      registerEvent('ha1p02801-rotate', 'pointerup', () => {
        let newElem = null
        if (curSel.type === 'elem1') {
          newElem = new HA1P02801ElementRotate90(curSel.x, curSel.y)
        } else if (curSel.type === 'elem1-1') {
          newElem = new HA1P02801ElementRotate180(curSel.x, curSel.y)
        } else if (curSel.type === 'elem1-2') {
          newElem = new HA1P02801Element(curSel.x, curSel.y)
        } else if (curSel.type === 'elem2') {
          newElem = new HA1P02801Element2Rotate90(curSel.x, curSel.y)
        } else if (curSel.type === 'elem2-1') {
          newElem = new HA1P02801Element2Rotate180(curSel.x, curSel.y)
        } else if (curSel.type === 'elem2-2') {
          newElem = new HA1P02801Element2(curSel.x, curSel.y)
        }
        this.selected = [newElem]
        this.grabbed = newElem
        super.removeElementById(curSel.id)
        super.addElement(newElem)
        this.render()
      })
    }
  }

  onClear() {
    super.setElements([])
    super.render()
  }

  initialize() {
    this.newCreateElement('ha1p02801-add-element-1', HA1P02801Element4, null, 60, 60)

    this.newCreateElement('ha1p02801-add-element-2', HA1P02801Element, null, 60, 120)

    this.newCreateElement('ha1p02801-add-element-3', HA1P02801Element2, null, 120, 160)

    this.newCreateElement('ha1p02801-add-element-4', HA1P02801Element3, null, 160, 160)
  }
}
