function convertPointerEvent(e) {
  e.offsetX = e.targetTouches[0]?.clientX
  e.offsetY = e.targetTouches[0]?.clientY
  e.pageX = e.targetTouches[0]?.pageX
  e.pageY = e.targetTouches[0]?.pageY

  if (e.changedTouches) {
    e.offsetX = e.changedTouches[0]?.clientX
    e.offsetY = e.changedTouches[0]?.clientY
    //e.pageX = e.changedTouches[0]?.pageX
    //e.pageY = e.changedTouches[0]?.pageY
  }
  return e
}

export var _root = document
export function setRoot(root) {
  _root = root
}

export function registerEventByQuerySelectorAll(tag, event, callback, root = _root) {
  Array.from(root.querySelectorAll(tag)).forEach((e) => {
    e.addEventListener(event, callback)
    // callback(e)
  })
}

export function registerEvent(tag, event, callback, root = _root) {
  root.getElementById(tag)?.addEventListener(
    event,
    (e) => {
      e.preventDefault()
      callback(e)
    },
    { passive: false },
  )
}

