// 정다각형 꼭지점 좌표
export function getRegularPolygonCoordinatesBySideLength(n, sideLength) {
  const radius = sideLength / (2 * Math.sin(Math.PI / n))
  const coordinates = []
  const angle = (2 * Math.PI) / n

  let internalAngle = 180 * (n - 2)
  if (n === 3 || n === 6) {
    internalAngle = 60
  } else if (n === 4) {
    internalAngle = 90
  }
  const rotationAngle = ((internalAngle / 2) * Math.PI) / 180
  for (let i = 0; i < n; i++) {
    const x = radius * Math.cos(i * angle)
    const y = radius * Math.sin(i * angle)

    const newX = x * Math.cos(rotationAngle) - y * Math.sin(rotationAngle)
    const newY = x * Math.sin(rotationAngle) + y * Math.cos(rotationAngle)
    coordinates.push([newX, newY])
  }

  if (n === 7 || n === 11 || n === 15) {
    return coordinates.map((coordinate) => getSymmetricPoints(coordinate, { cy: 0 }))
  } else {
    return coordinates
  }
}

// 정다각형 꼭지점 좌표
export function getRegularPolygonCoordinatesByRadius(n, radius = 150) {
  const coordinates = []
  const angle = (2 * Math.PI) / n

  let internalAngle = 180 * (n - 2)
  if (n === 3 || n === 6) {
    internalAngle = 60
  } else if (n === 4) {
    internalAngle = 90
  }

  const rotationAngle = ((internalAngle / 2) * Math.PI) / 180
  for (let i = 0; i < n; i++) {
    const x = radius * Math.cos(i * angle)
    const y = radius * Math.sin(i * angle)

    const newX = x * Math.cos(rotationAngle) - y * Math.sin(rotationAngle)
    const newY = x * Math.sin(rotationAngle) + y * Math.cos(rotationAngle)

    coordinates.push([newX, newY])
  }

  if (n === 7 || n === 11 || n === 15) {
    return coordinates.map((coordinate) => getSymmetricPoints(coordinate, { cy: 0 }))
  } else {
    return coordinates
  }
}

// 다각형 꼭지점울 둘러싸고있는 직사각형 좌표
export function getSurroundVertexCoordinate(arr) {
  return arr.reduce(
    (acc, [x, y]) => {
      return {
        minX: Math.min(acc.minX, x),
        maxX: Math.max(acc.maxX, x),
        minY: Math.min(acc.minY, y),
        maxY: Math.max(acc.maxY, y),
      }
    },
    {
      minX: arr[0][0],
      maxX: arr[0][0],
      minY: arr[0][1],
      maxY: arr[0][1],
    },
  )
}

export function getCoordinatesXY(arr, x, y) {
  return arr.map(([a, b]) => {
    return [a + x, b + y]
  })
}
// 다각형 꼭지점을 잇는 path 경로
export function getPathDistanceAttribute(arr) {
  return (
    arr.reduce((acc, [x, y], index) => {
      return index === 0 ? `M${x},${y}` : `${acc} L${x},${y}`
    }, '') + ' Z'
  )
}

export function getPathDistanceAttributeByRx(arr, rx = 0) {
  return (
    arr.reduce((acc, [x, y], index, curArr) => {
      if (index === 0) {
        return `M${x + rx},${y}`
      } else if (index === 1) {
        return `${acc} L${x - rx},${y} Q${x},${y} ${x},${y - rx}`
      } else if (index === 2) {
        return `${acc} L${x},${y + rx} Q${x},${y} ${x - rx},${y}`
      } else if (index === 3) {
        return `
        ${acc} L${rx},${y} Q${x},${y} ${x},${y + rx}
        L${curArr[0][0]},${-rx}
        Q${curArr[0][0]},${curArr[0][1]} ${curArr[0][0] + rx},${curArr[0][1]}`
      } else {
        return ''
      }
    }, '') + ' Z'
  )
}

// 직사각형을 a만큼 넓힌 좌표
export function getExpandRectangleCoordinates(arr, a) {
  const [x1, y1] = arr[0]
  const [x2, y2] = arr[1]
  const [x3, y3] = arr[2]
  const [x4, y4] = arr[3]

  const expandedRectangle = [
    [x1 - a, y1 - a],
    [x2 + a, y2 - a],
    [x3 + a, y3 + a],
    [x4 - a, y4 + a],
  ]

  return expandedRectangle
}

// 직사각형 각변의 중점 좌표
export function getRectangleMidpoints(arr) {
  const midpoints = []
  for (let i = 0; i < arr.length; i++) {
    const currentPoint = arr[i]
    const nextPoint = arr[(i + 1) % arr.length]
    const midpointX = (currentPoint[0] + nextPoint[0]) / 2
    const midpointY = (currentPoint[1] + nextPoint[1]) / 2
    midpoints.push([midpointX, midpointY])
  }
  return midpoints
}

// x1, y1에서 angleDeg만큼 회전한 새로운 좌표
export function getRotatePoint(x1, y1, angleDeg) {
  const deg = ((angleDeg % 360) + 360) % 360
  const angleRad = (deg * Math.PI) / 180

  const cosTheta = Math.cos(angleRad)
  const sinTheta = Math.sin(angleRad)

  const newX = x1 * cosTheta - y1 * sinTheta
  const newY = x1 * sinTheta + y1 * cosTheta

  return { x: newX, y: newY }
}

export function rotatePointAroundCenter(x1, y1, cx, cy, angleDeg) {
  // 각도를 라디안으로 변환
  const deg = ((angleDeg % 360) + 360) % 360
  const angleRad = (deg * Math.PI) / 180

  // 코사인과 사인 값 계산
  const cosTheta = Math.cos(angleRad)
  const sinTheta = Math.sin(angleRad)

  // 원점을 기준으로 회전하기 위해 점을 평행 이동
  const translatedX = x1 - cx
  const translatedY = y1 - cy

  // 회전 변환
  const rotatedX = translatedX * cosTheta - translatedY * sinTheta
  const rotatedY = translatedX * sinTheta + translatedY * cosTheta

  // 다시 원래의 중심으로 평행 이동
  const newX = rotatedX + cx
  const newY = rotatedY + cy

  return { x: newX, y: newY }
}

// 두 점사이의 각도
export function getCalculateRotation(point1, point2) {
  const deltaX = point2.x - point1.x
  const deltaY = point2.y - point1.y
  const angleRad = Math.atan2(deltaX, -deltaY)
  let angleDeg = (angleRad * 180) / Math.PI
  if (angleDeg < 0) {
    angleDeg += 360
  }
  return Math.round(angleDeg)
}

// 중심에서 두점사이의 각도
// flg = false, 시계방향으로 각도 계산
// flg = true, 시계반대방향으로 각도 계산
export function getCalculateRotationFromCenter(center, point1, point2, flg = false) {
  const vector1 = {
    x: point1.x - center.x,
    y: point1.y - center.y,
  }

  const vector2 = {
    x: point2.x - center.x,
    y: point2.y - center.y,
  }

  const angleRad1 = Math.atan2(vector1.y, vector1.x)
  const angleRad2 = Math.atan2(vector2.y, vector2.x)

  let angleRad = flg ? angleRad1 - angleRad2 : angleRad2 - angleRad1

  if (angleRad < 0) {
    angleRad += 2 * Math.PI
  }

  let angleDeg = (angleRad * 180) / Math.PI

  return Math.round(angleDeg)
}

export function findIntersection(point1, point2, point3, point4) {
  const { x: x1, y: y1 } = point1
  const { x: x2, y: y2 } = point2
  const { x: x3, y: y3 } = point3
  const { x: x4, y: y4 } = point4

  const denominator = (y4 - y3) * (x2 - x1) - (x4 - x3) * (y2 - y1)
  if (denominator === 0) {
    return { x: 0, y: 0 }
  }

  const ua = ((x4 - x3) * (y1 - y3) - (y4 - y3) * (x1 - x3)) / denominator
  const ub = ((x2 - x1) * (y1 - y3) - (y2 - y1) * (x1 - x3)) / denominator

  // 교점이 두 선분 모두에 속하지 않을 때
  if (ua < 0 || ua > 1 || ub < 0 || ub > 1) {
    return { x: 0, y: 0 }
  }

  // 교점 좌표 계산
  const x = x1 + ua * (x2 - x1)
  const y = y1 + ua * (y2 - y1)

  return { x, y }
}

export function pointOnLineSegment(point1, point2, point) {
  const { x: x1, y: y1 } = point1
  const { x: x2, y: y2 } = point2
  const { x, y } = point

  if (x1 === x2 && y1 === y2) {
    return x === x1 && y === y1
  }

  const m = (y2 - y1) / (x2 - x1)
  const b = y1 - m * x1

  // 새로운 점 C가 선분 위
  return (
    Math.abs(y - (m * x + b)) < 1 &&
    x >= Math.min(x1, x2) &&
    x <= Math.max(x1, x2) &&
    y >= Math.min(y1, y2) &&
    y <= Math.max(y1, y2)
  )
}

export function calculatePointOnCircle(point1, point2, r) {
  const angle = Math.atan2(point2.y - point1.y, point2.x - point1.x)
  const x = point1.x + r * Math.cos(angle)
  const y = point1.y + r * Math.sin(angle)
  return { x, y }
}

// 두 점 사이의 거리
export function getDistanceBetweenTwoPoints(point1, point2) {
  const dx = point2.x - point1.x
  const dy = point2.y - point1.y
  return Math.sqrt(dx * dx + dy * dy)
}

// 두점 사이의 거리가 스냅되는지 안되는지 확인
export function getSnapCoordinateTwoPoint(point1, point2, distance) {
  return Math.abs(point1[0] - point2[0]) <= distance && Math.abs(point1[1] - point2[1]) <= distance
}

// 그리드(배경)에 스냅되는 좌표
export function getGridSnapCoordinate(x, y, grid, distance) {
  const snapX = Math.round(x / grid) * grid
  const snapY = Math.round(y / grid) * grid

  if (Math.abs(snapX - x) <= distance && Math.abs(snapY - y) <= distance) {
    return { snapX, snapY }
  } else {
    return { snapX: x, snapY: y }
  }
}

// 회전된 새로운 좌표
export function getRotatedCoordinates(coordinates, x, y, rotate) {
  return coordinates?.map(([pointX, pointY]) => {
    const { x: rotatedX, y: rotatedY } = getRotatePoint(pointX, pointY, rotate)
    return [Math.round(rotatedX + x), Math.round(rotatedY + y)]
  })
}

// 두 직사각형의 교집합 영역 계산
export function intersectionArea(rect1, rect2) {
  const {
    minX: rect1MinX,
    maxX: rect1MaxX,
    minY: rect1MinY,
    maxY: rect1MaxY,
  } = getSurroundVertexCoordinate(rect1)
  const {
    minX: rect2MinX,
    maxX: rect2MaxX,
    minY: rect2MinY,
    maxY: rect2MaxY,
  } = getSurroundVertexCoordinate(rect2)
  const x_overlap = Math.max(0, Math.min(rect2MaxX, rect1MaxX) - Math.max(rect2MinX, rect1MinX))
  const y_overlap = Math.max(0, Math.min(rect2MaxY, rect1MaxY) - Math.max(rect2MinY, rect1MinY))
  return x_overlap * y_overlap
}

// 사각형과 다른 사각형들의 교집합 계산
export function calculateOverlap(rect, rects) {
  let maxOverlap = 0
  let maxOverlapIndex = -1

  for (let i = 0; i < rects.length; i++) {
    const { coordinates, x, y, rotate } = rects[i]
    const newCoordinates = getRotatedCoordinates(coordinates, x, y, rotate)
    const overlap = intersectionArea(rect, newCoordinates)
    if (overlap > maxOverlap) {
      maxOverlap = overlap
      maxOverlapIndex = i
    }
  }

  return { index: maxOverlapIndex, overlapArea: maxOverlap }
}

// 다각형의 중심
export function getCenterCoordinate(arr) {
  if (arr?.length < 3) {
    return
  }
  const { minX, minY, maxX, maxY } = getSurroundVertexCoordinate(arr)
  const cx = (minX + maxX) / 2
  const cy = (minY + maxY) / 2
  return { cx, cy }
}

// 다각형의 무게중심
export function getCenterOfGravity(arr) {
  const length = arr.length
  const newArr = arr.reduce((acc, cur) => [acc[0] + cur[0], acc[1] + cur[1]], [0, 0])
  return [newArr[0] / length, newArr[1] / length]
}

// 정다각형의 무게중심에서 꼭지점 까지 거리를 알 때, 한변의 길이를 구함
// radius: 무게중심에서 꼭지점까지 거리
export function calculateSideLength(n, radius, perLength = 50) {
  const value = 2 * radius * Math.sin(Math.PI / n)
  return Math.floor(value / perLength) * perLength
}

// 다각형을 둘러싸는 직사각형의 좌표
export function getSurroundCoordinates(coordinates, rotate) {
  const { minX, maxX, minY, maxY } = getSurroundVertexCoordinate(coordinates)
  const arr = [
    [minX, minY],
    [maxX, minY],
    [maxX, maxY],
    [minX, maxY],
  ]
  return getRotatedCoordinates(arr, 0, 0, rotate)
}

// 회전축 좌표
export function getRotateCircleCoordinate(coordinates, rotate) {
  const { minX, maxX, minY } = getSurroundVertexCoordinate(coordinates)
  return getRotatePoint((minX + maxX) / 2, minY - 40, rotate)
}

// 사각형안에 점이 들어가있는지 없는지 확인
export function isPointInPolygon(point, polygon) {
  const [x, y] = point
  let inside = false
  const polygonLength = polygon.length
  for (let i = 0, j = polygonLength - 1; i < polygonLength; j = i++) {
    const [xi, yi] = polygon[i]
    const [xj, yj] = polygon[j]
    const isYInside = yi > y !== yj > y
    const isXInside = x < ((xj - xi) * (y - yi)) / (yj - yi) + xi
    const intersect = isYInside && isXInside
    if (intersect) {
      inside = !inside
    }
  }
  return inside
}

// x, y축 대칭된 좌표
export function getSymmetricPoints(point, symmetricPoint) {
  if (!symmetricPoint) return point

  const [x, y] = point
  const { cx, cy } = symmetricPoint

  const newX = cx !== undefined ? (cy === 0 ? -x : 2 * cx - x) : x
  const newY = cy !== undefined ? (cx === 0 ? -y : 2 * cy - y) : y

  return [newX, newY]
}

// 두점과 수직이고 point2를 지나는 선분의 좌표
export function findPerpendicularPoint(point1, point2, l = 100) {
  const [x1, y1] = point1
  const [x2, y2] = point2

  if (x2 === x1) {
    return [
      { x: x1 - l / 10, y: y2 },
      { x: x2 + l / 10, y: y2 },
    ]
  }
  if (y2 === y1) {
    return [
      { x: x2, y: y1 - l / 10 },
      { x: x2, y: y2 + l / 10 },
    ]
  }
  // 두 점을 지나는 직선의 기울기
  const slope = (y2 - y1) / (x2 - x1)

  // 중점 좌표 계산
  const midX = (x1 + x2) / 2
  const midY = (y1 + y2) / 2

  // 수직인 직선의 기울기
  const perpendicularSlope = -1 / slope

  // 점 (x2, y2)를 중심으로 하는 길이가 l인 선분의 좌표 계산
  const deltaX = Math.sqrt(l / (1 + perpendicularSlope * perpendicularSlope))
  const deltaY = perpendicularSlope * deltaX

  const intersectionX =
    (perpendicularSlope * x2 - slope * midX + midY - y2) / (perpendicularSlope - slope)
  const intersectionY = perpendicularSlope * (intersectionX - x2) + y2

  // 선분의 끝점
  const end1X = intersectionX + deltaX
  const end1Y = intersectionY + deltaY
  const end2X = intersectionX - deltaX
  const end2Y = intersectionY - deltaY

  return [
    { x: end1X, y: end1Y },
    { x: end2X, y: end2Y },
  ]
}

