export function fillRectangleSvg(startX, startY, width, height, perWidth, perHeight, gap = 20) {
  let x = startX
  let y = startY

  let svg = ''
  while (y + perHeight <= height) {
    while (x + perWidth <= width) {
      svg += `
        <rect x="${x}" y="${y}" width="${perWidth}" height="${perHeight}" rx="10" stroke="gray" stroke-width="2" fill="none" />
      `
      x += perWidth + gap
    }
    y += perHeight + gap
    x = startX
  }
  return svg
}

export function convertAngle(angle, step = 5) {
  const moduloAngle = angle % 360

  if (
    moduloAngle >= 360 - step ||
    moduloAngle <= step ||
    (moduloAngle >= 0 && moduloAngle <= step)
  ) {
    return 0
  } else if (moduloAngle >= 90 - step && moduloAngle <= 90 + step) {
    return 90
  } else if (moduloAngle >= 180 - step && moduloAngle <= 180 + step) {
    return 180
  } else if (moduloAngle >= 270 - step && moduloAngle <= 270 + step) {
    return 270
  } else {
    return moduloAngle
  }
}

export function createSVGElement(elementType, attributes, events) {
  const element = document.createElementNS('http://www.w3.org/2000/svg', elementType)
  for (const key in attributes) {
    element.setAttribute(key, attributes[key])
  }
  for (const eventName in events) {
    element.addEventListener(eventName, events[eventName])
  }
  return element
}

export function toggleActive(e) {
  const items = getElementsByQuerySelectorAll('.test')
  const isActive = e.target.classList.contains('active')

  for (const item of items) {
    item.classList.remove('active')
  }
  if (!isActive) {
    e.target.classList.toggle('active')
  }
}

// path sweep-flag값 계산
export function calcSweepFlag(point1, point2, point3) {
  if (point3) {
    const AB = { x: point2.x - point1.x, y: point2.y - point1.y }
    const BC = { x: point3.x - point2.x, y: point3.y - point2.y }
    const crossProduct = AB.x * BC.y - AB.y * BC.x
    return crossProduct > 0 ? 1 : 0
  } else {
    const crossProduct = point1.x * point2.y - point2.x * point1.y
    return crossProduct > 0 ? 1 : 0
  }
}

// path large-arc-flag값 계산
export function calcLargeArcFlag(sweepFlag, center, point1, point2) {
  const angleDeg = getCalculateRotationFromCenter(center, point1, point2)
  const angle = sweepFlag === 1 ? 360 - angleDeg : angleDeg
  return angle > 180 ? 0 : 1
}

// 평균
export function getAverage(arr, n = 2) {
  if (arr.length === 0) return 0
  const newArr = arr.filter((item) => item)
  const sum = newArr.map((item) => Number(item)).reduce((acc, curr) => acc + curr, 0)
  return (sum / newArr.length).toFixed(n).toString()
}

// 중앙값
export function getMedian(arr) {
  if (arr.length === 0) return 0
  const sorted = arr.filter((item) => item).sort(customSort)
  const middle = Math.floor(sorted.length / 2)

  if (sorted.length % 2 === 0) {
    return [sorted[middle - 1], sorted[middle]]
  } else {
    return sorted[middle]
  }
}

// 최빈값
export function getMode(arr) {
  let counts = {}
  let maxCount = 0
  let modes = []

  arr.forEach((item) => {
    const key = item.trim() // 앞뒤 공백 제거
    counts[key] = (counts[key] || 0) + 1
    if (counts[key] > maxCount) {
      maxCount = counts[key]
    }
  })

  for (const key in counts) {
    if (counts[key] === maxCount) {
      modes.push(key)
    }
  }

  return modes
}

export function saveInputItem(target) {
  if (target && target.inputTargetClassName) {
    saveInputEditedItem(target)
  }
}

export function saveInputEditedItem(target) {
  const container = getElement(target.id)
  const elements = container.querySelectorAll('.input-target')
  elements.forEach((element) => {
    const text = element?.textContent.trim()
    const [name, parentIndex, index] = element.classList.value.split(' ')[1].split('-')

    if (element) {
      element.textContent = text
      if (name && parentIndex && index) {
        if (!target[name][parentIndex]) target[name][parentIndex] = []
        target[name][parentIndex][index] = text
        return
      }

      if (name && parentIndex && !index) {
        if (!isNaN(Number(parentIndex))) {
          target[name][parentIndex] = text
        }
        return
      }

      if (name && !parentIndex && !index) {
        target[name] = text
        const str = name.substring(5)
        const n = str ? Number(str) : 0

        if (name.startsWith('title')) {
          target[name] = target.titleOriginalText[n] ?? ''
        }
        return
      }
    }
  })
}

