export function customSort(a, b) {
  // 두 값을 숫자로 변환해서 비교
  let numA = Number(a)
  let numB = Number(b)

  // 둘 다 숫자인 경우 숫자로 비교
  if (!isNaN(numA) && !isNaN(numB)) {
    return numA - numB
  }

  // 둘 다 숫자가 아닌 경우 문자열로 비교
  if (isNaN(numA) && isNaN(numB)) {
    // 한글과 영어를 구분하여 비교
    let isKoreanA = /[가-힣]/.test(a)
    let isKoreanB = /[가-힣]/.test(b)

    // 둘 다 한글인 경우
    if (isKoreanA && isKoreanB) {
      return a.localeCompare(b, 'ko')
    }

    // 둘 다 영어인 경우
    if (!isKoreanA && !isKoreanB) {
      return a.localeCompare(b)
    }

    // 하나는 한글이고 하나는 영어인 경우, 영어를 우선
    return isKoreanA ? 1 : -1
  }

  // 하나는 숫자고 하나는 문자열인 경우, 숫자를 우선
  return !isNaN(numA) ? -1 : 1
}

export function customSortWithModes(arr) {
  const modes = getMode(arr)

  // 정렬 함수
  function customSort(a, b) {
    // 최빈값들을 우선 정렬
    if (modes.includes(a) && modes.includes(b)) {
      return modes.indexOf(a) - modes.indexOf(b)
    }
    if (modes.includes(a)) {
      return -1
    }
    if (modes.includes(b)) {
      return 1
    }

    // 둘 값을 숫자로 변환해서 비교
    let numA = Number(a)
    let numB = Number(b)

    // 둘 다 숫자인 경우 숫자로 비교
    if (!isNaN(numA) && !isNaN(numB)) {
      return numA - numB
    }

    // 둘 다 숫자가 아닌 경우 문자열로 비교
    if (isNaN(numA) && isNaN(numB)) {
      // 한글과 영어를 구분하여 비교
      let isKoreanA = /[가-힣]/.test(a)
      let isKoreanB = /[가-힣]/.test(b)

      // 둘 다 한글인 경우
      if (isKoreanA && isKoreanB) {
        return a.localeCompare(b, 'ko')
      }

      // 둘 다 영어인 경우
      if (!isKoreanA && !isKoreanB) {
        return a.localeCompare(b)
      }

      // 하나는 한글이고 하나는 영어인 경우, 영어를 우선
      return isKoreanA ? 1 : -1
    }

    // 하나는 숫자고 하나는 문자열인 경우, 숫자를 우선
    return !isNaN(numA) ? -1 : 1
  }

  // 배열 정렬
  arr.sort(customSort)

  return arr
}

export function frequencyCalculate(arr) {
  const rArr = []
  // Calculate the sum of all numerical values in frequencyItems
  const sum = arr
    .map((item) => (!isNaN(item) ? Number(item) : 0))
    .reduce((acc, cur) => acc + cur, 0)

  // Calculate initial relative frequencies with higher precision
  const preciseRelativeFrequencies = []
  arr.forEach((item, i) => {
    if (!isNaN(item) && sum !== 0) {
      const preciseFrequency = Number(item) / sum
      preciseRelativeFrequencies.push(preciseFrequency)
    } else {
      rArr[i] = ''
    }
  })

  // Calculate rounded relative frequencies
  const roundedRelativeFrequencies = preciseRelativeFrequencies.map(
    (freq) => Math.round(freq * 10000) / 10000,
  )
  const roundedSum = roundedRelativeFrequencies.reduce((acc, cur) => acc + cur, 0)

  // Adjust the frequencies to make sure the sum is 1
  let difference = Math.round((1 - roundedSum) * 10000) / 10000
  if (difference !== 0) {
    for (let i = 0; i < roundedRelativeFrequencies.length; i++) {
      if (preciseRelativeFrequencies[i] !== 0) {
        roundedRelativeFrequencies[i] =
          Math.round((roundedRelativeFrequencies[i] + difference) * 10000) / 10000
        break
      }
    }
  }

  // Assign rounded relative frequencies to the table
  return roundedRelativeFrequencies
}

