export function getAverageAndMedianAndMode(arr) {
  const items = arr.filter((item) => item)

  const average = getAverage(items) ?? 0
  const median = getMedian(items) ?? 0
  const mode = getMode(items) ?? 0

  const isOnlyNumber = Array.isArray(median) && median.every((item) => !isNaN(item))

  const resultTmp = [average, median, mode]
  const result = [
    average,
    isOnlyNumber
      ? median.map((item) => Number(item)).reduce((acc, cur) => acc + cur, 0) / median.length
      : median,
    mode,
  ]

  return { resultTmp, result }
}

export function isRectangleInside(bigRect, smallRect) {
  // 큰 사각형의 경계 좌표 계산
  const bigMinX = Math.min(bigRect[0][0], bigRect[1][0], bigRect[2][0], bigRect[3][0])
  const bigMaxX = Math.max(bigRect[0][0], bigRect[1][0], bigRect[2][0], bigRect[3][0])
  const bigMinY = Math.min(bigRect[0][1], bigRect[1][1], bigRect[2][1], bigRect[3][1])
  const bigMaxY = Math.max(bigRect[0][1], bigRect[1][1], bigRect[2][1], bigRect[3][1])

  // 작은 사각형의 모든 꼭짓점이 큰 사각형의 경계 내에 있는지 확인
  for (let i = 0; i < smallRect.length; i++) {
    const x = smallRect[i][0]
    const y = smallRect[i][1]

    if (x < bigMinX || x > bigMaxX || y < bigMinY || y > bigMaxY) {
      return false
    }
  }
  return true
}

// 두 원이 접하는지 확인하는 함수
export function areTouching(c1, c2) {
  const distanceSq = (c2.x + c2.cx - (c1.x + c1.cx)) ** 2 + (c2.cy + c2.y - (c1.cy + c1.y)) ** 2
  const radiusSum = c1.r + c2.r
  const radiusDiff = Math.abs(c2.r - c1.r)
  return distanceSq <= radiusSum ** 2 && distanceSq >= radiusDiff ** 2
}

// 배열에 있는 원들이 접하는지 확인하는 함수
export function classifyCircles(circles) {
  let touchingCircles = []
  let nonTouchingCircles = []

  let assigned = new Array(circles.length).fill(false)

  for (const circle of circles) circle.count = 0

  for (let i = 0; i < circles.length; i++) {
    if (!assigned[i]) {
      let group = [circles[i]]
      assigned[i] = true
      for (let j = 0; j < group.length; j++) {
        for (let k = 0; k < circles.length; k++) {
          if (i === k) continue
          if (!assigned[k] && areTouching(group[j], circles[k])) {
            group.push(circles[k])
            group[j].count++
            circles[k].count++
            assigned[k] = true
          }
        }
      }

      if (group.length > 1) {
        touchingCircles.push(group)
      } else {
        nonTouchingCircles.push(group[0])
      }
    }
  }

  return {
    touchingCircles: touchingCircles,
    nonTouchingCircles: nonTouchingCircles,
  }
}

export function getSvgSurroundCoordinatesByTag(id, target) {
  if (id) {
    if (target) {
      const { x, y, width, height } = getElement(id)?.querySelector(`.${target}`).getBBox()
      return [
        [x, y],
        [x + width, y],
        [x + width, y + height],
        [x, y + height],
      ]
    } else {
      const { x, y, width, height } = getElement(id)?.getBBox()
      return [
        [x, y],
        [x + width, y],
        [x + width, y + height],
        [x, y + height],
      ]
    }
  }
}

