export function isOverlapping(rect1, rect2) {
  // 직사각형이 겹치지 않는 조건
  if (rect1.x >= rect2.x + rect2.width || rect2.x >= rect1.x + rect1.width) {
    return false
  }
  if (rect1.y >= rect2.y + rect2.height || rect2.y >= rect1.y + rect1.height) {
    return false
  }
  // 위 조건에 해당하지 않으면 겹침
  return true
}

export function completeArithmeticSequence(array) {
  const arr = [...new Set(array)].sort((a, b) => a - b)
  const commonDifference = arr[1] - arr[0]
  const minValue = arr[0]
  const maxValue = arr[arr.length - 1]

  const completeArray = []
  for (let i = minValue; i <= maxValue; i += commonDifference) {
    completeArray.push(i)
  }

  return completeArray
}
